.statistics {
    width: 350px;
    margin-top: 30px;
    padding-bottom: 20px;
    font-size: 18px;
    background-color: gray;
    border-radius: 20px;
    
}

.title {
    padding: 15px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    color: orange;
}

.statlist {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style: none;
}


.item {
    margin: 0;
    background-color: blue;
    width: 70px;
}

.label {
    display: block;
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: center;
}

.percentage {
    display: block;
    padding-top: 8px;
    padding-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    color: white;
}